import { useState, useEffect } from "react";
import styled from "styled-components";
import Navbar from "../components/Navbar";
import Announcement from "../components/Announcement";
import Contacts from "../components/Contacts";
import Newsletter from "../components/Newsletter";
import Footer from "../components/Footer";
import { mobile } from "../responsive";
import { useLocation } from "react-router";
import { constLang } from "../constant/language";
import { utilService } from '../services/util.service'
import { SelectOption } from "../components/SelectOption";
import { contactService } from "../services/contact.service";
import { JsConst } from "../constant/JsConst";

const Container = styled.div``;

const Title = styled.h1`
  margin: 20px;
`;

const FilterContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Filter = styled.div`
  margin: 20px;
  ${mobile({ width: "0px 20px", display: "flex", flexDirection: "column" })}
`;

const FilterText = styled.span`
  font-size: 20px;
  font-weight: 600;
  margin-right: 20px;
  ${mobile({ marginRight: "0px" })}
`;

const Select = styled.select`
  padding: 10px;
  margin-right: 20px;
  ${mobile({ margin: "10px 0px" })}
`;

const Button = styled.button`
  padding: 10px;
  background-color: transparent;
  border: 1px solid black;
  cursor: pointer;
  height: 40px;
`;

const Option = styled.option``;


const ContactListPage = () => {
  const location = useLocation();
  const cat = location.pathname.split("/")[2];
  const [filters, setFilters] = useState({});
  const [sort, setSort] = useState("newest");

  useEffect(() => {
    utilService.scrollToTop()
  }, [])

  const handleFilters = ({ target }) => {
    const { value, name } = target
    setFilters({ ...filters, [name]: value, });
  };

  const handleClearFilter = () => {
    setFilters({})
    clearSelects()
  }

  const clearSelects = () => {
    const elSelectes = document.querySelector('.filter')
    const selectTags = elSelectes.getElementsByTagName("select");
    for (var i = 0; i < selectTags.length; i++) {
      selectTags[i].selectedIndex = 0;
    }
  }

  return (
    <Container>
      <Navbar />
      <Announcement />
      <Title>{utilService.getFirstLetterUppercase(cat)}</Title>
      <FilterContainer>
        <Filter className="filter">
          <FilterText>Filter {constLang.Contacts}:</FilterText>

          <SelectOption
            cb={handleFilters}
            options={contactService.getCategoriesOptions(cat)}
            name={JsConst.CATEGORIES}
            title={constLang.COMPANY}
          />

          <SelectOption
            cb={handleFilters}
            options={contactService.getJobTitles()}
            name={constLang.JOB_TITLE}
            title={constLang.Job_Title}
          />

          <Button onClick={handleClearFilter}>Show All</Button>
        </Filter>
        <Filter>
          <FilterText>Sort {constLang.Contacts}:</FilterText>
          <Select onChange={(e) => setSort(e.target.value)}>
            <Option value="newest">Newest</Option>
            <Option value="asc">Price (asc)</Option>
            <Option value="desc">Price (desc)</Option>
          </Select>
        </Filter>
      </FilterContainer>
      <Contacts cat={cat} filters={filters} sort={sort} />
      <Newsletter />
      <Footer />
    </Container>
  );
};

export default ContactListPage;


