import { ContactsEP } from '../config/appConfig'
import { constLang } from '../constant/language';
import { httpService } from "./http.service";

export const contactService = {
    query,
    queryByCategory,
    getCategoriesOptions,
    getJobTitles,
}


export async function query(filterBy = {}) {
    try {
        const res = await httpService.get(ContactsEP.get)
        return res
    } catch (err) {
        throw err
    }
}

async function queryByCategory(cat) {
    try {
        const res = await httpService.get(`${ContactsEP.get}/${cat}`)
        return res
    } catch (err) {
        throw err
    }
}

function getCategoriesOptions(cat) {
    switch (cat) {
        case 'bank':
            return [
                { id: 1, text: constLang.BANK_HAPOALIM },
                { id: 2, text: constLang.BANK_DISCOUNT },
                { id: 3, text: constLang.ISRACARD },
                { id: 4, text: constLang.BANK_LEUMI },
                { id: 5, text: constLang.BANK_MIZRAHI },
            ]
        case 'insurance':
            return [
                { id: 1, text: constLang.CLAL },
                { id: 2, text: constLang.MIGDAL },
                { id: 3, text: constLang.AYALON }
            ]
        case 'goverment':
            return [
                { id: 1, text: constLang.MINISTRY_OF_EDUCATION },
                { id: 2, text: constLang.MINISTRY_OF_FINANCE }
            ]
        default:
            return []
    }
}

function getJobTitles() {
    return [
        { id: 1, text: constLang.CTO },
        { id: 2, text: constLang.CIO },
        { id: 3, text: constLang.CFO },
        { id: 4, text: constLang.CISO },
    ]
}